.button{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 15px;
    color: black;
    background-color: white;
    width: 80%;
    height: 30px;
    border-radius: 10px;
}
.button > img{
    margin-right: 10px;
}
.button:hover{
    cursor: pointer;
}